.selector-wrapper {
  position: relative;
  width: 100%;
  outline: none;
}
.select-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  border-bottom: 1px solid #999;
  height: 23px;
  overflow: hidden;
}
.select-box:hover,
.select-box.focusing,
.select-box:active {
  border-color: #5582F3;
  cursor: pointer;
}
.custom-select {
  flex: 1;
  color: #333;
  font-size: 14px;
}
.arrow-icon {
  color: #ccc;
  font-size: 12px;
  transition: transform 0.2s ease;
}
.arrow-up {
  transform: rotate(180deg);
}
.select-option {
  width: 100%;
  max-height: 110px;
  overflow-y: auto;
  position: absolute;
  top: 24px;
  left: 0;
  z-index: 9;
  background: #fff;
  box-shadow: 0 2px 9px 0 rgba(175, 175, 175, 0.5);
  padding: 5px 10px 20px;
}
.option-item {
  line-height: 30px;
  cursor: pointer;
}
.ellipse-text {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.empty-text {
  font-size: 12px;
  color: #999;
  text-align: center;
}
.ant-checkbox-wrapper {
  margin-bottom: 8px;
  font-size: 12px;
}
.ant-divider-horizontal {
  margin: 0;
}
.select-option-tip {
  position: absolute;
  bottom: 10px;
  left: 80px;
  font-size: 12px;
  color: #CCC;
}
.select-option-tip .key {
  color: #5582F3;
}
