.wrapper_left {
  width: 350px;
  height: 100%;
  padding: 15px 80px 15px 20px;
  background: #fff;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.15);
  margin-right: 10px;
}
.top-title {
  font-size: 14px;
  color: #333;
}
.form-item-ctn {
  margin: 15px 0 22px;
}
.form-title {
  font-size: 12px;
  color: #666;
  margin-bottom: 3px;
}
.form-value {
  font-size: 14px;
  color: #333;
  line-height: 22px;
  border-bottom: 1px solid #999;
}
.detail-label {
  display: flex;
  font-size: 14px;
  color: #666;
  margin-bottom: 20px;
}
.detail-key {
  color: #5582f3;
  margin-left: auto;
  margin-right: 12px;
}
