.points-ctn {
  position: relative;
  height: 100%;
  flex: 1;
  background-color: #fff;
  overflow: hidden;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.15);
}
.top-title {
  position: absolute;
  top: 15px;
  left: 20px;
  font-size: 14px;
  color: #333;
}
.canvas-ctn {
  position: relative;
  height: 100%;
}
.img-hover {
  position: absolute;
  display: none;
  width: 60px;
  height: 57px;
  transform: scale(1.5);
}
.info-ctn {
  position: absolute;
  width: 330px;
  z-index: 1;
  font-size: 12px;
  color: #333;
  padding: 10px 20px 2px 30px;
  background: #fff;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.3);
}
.info-title {
  position: relative;
  margin-bottom: 10px;
  font-size: 14px;
}
.info-title::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 100%;
  background: #26b175;
  left: -12px;
  top: 4px;
}
.info-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
  color: #43506C;
  word-break: break-all;
}
.info-item-key {
  width: 90px;
  color: #999;
  flex-shrink: 0;
}
.ip_addr_ctn {
  display: flex;
  align-items: center;
}
.ip-addr-block {
  width: 8px;
  height: 8px;
  margin-right: 2px;
}
